import React from "react";
import {ErrorMessage} from "@hookform/error-message";
import cn from "classnames";
import styles from "./CreateField.module.scss";


const FormControl = ({errors, name, children}) => {
    return (
        <div className={cn(styles.input, {
            [styles.error]: errors[name]
        })}>
            {children}
            <ErrorMessage errors={errors} name={name}/>
        </div>
    )
}

export const CreateInput = (name, placeholder, type, errors = {}, ref) => {
    return (
        <FormControl errors={errors} name={name}>
            <input name={name} placeholder={placeholder} type={type} ref={ref}/>
        </FormControl>
    )
}

export const CreateTextarea = (name, placeholder, errors = {}, ref) => {
    return (
        <FormControl errors={errors} name={name}>
            <textarea name={name} placeholder={placeholder} ref={ref}/>
        </FormControl>
    )
}
